const height = document.getElementById('height');
const width = document.getElementById('width');
const length = document.getElementById('length');
const tree = document.getElementById('tree');

export default function calcBoxArea() {
	let sum = 0;
	//return calculated box area if all inputs are filled
	if (height.value && width.value && length.value) {
		sum += height.value*width.value * 2 +
			width.value*length.value * 2 +
			length.value*height.value * 2;
		return tree.checked  ? sum * 5 : sum;
	}
	return 0;
}