const headerChild = document.querySelector('header').children;
const description = document.querySelectorAll('.description');
const ledDescription = document.querySelector('.led').firstChild.firstChild;

document.addEventListener('scroll', e => {
	const pYO = window.pageYOffset;
	//move header items
	for (let i of headerChild) {
		i.style.opacity = 20 / pYO;
		i.style.transform = `translateX(${pYO * .5}%) translateY(${pYO}px)`;
	}
	//show descriptions
	for (let i of description) {
		//element shoul be caught on center of screen
		if ((i.getBoundingClientRect().top + i.clientHeight / 2 - window.innerHeight / 2) < 0) {
			i.firstElementChild.classList.add('polygon');
			i.firstElementChild.firstElementChild.classList.add('hidden');
			ledDescription.style.cssText = 'position: sticky; top: 30%';
		}
	}
});