import loadLedSection from './ledCatalogList.js';

const body = document.body;

//open and/or load LED catalog
[...document.querySelectorAll('.des-wrapper'), document.getElementById('product-cross')].forEach( i =>
	i.addEventListener('click', e => {
		body.classList.toggle('led');
		if (e.currentTarget.className == 'des-wrapper') {
			loadLedSection(e.currentTarget.getAttribute('data-product'));
		}
	}
));

//open purchase
[document.getElementById('basket'), document.getElementById('purchase-cross')].forEach( i =>
	i.addEventListener('click', e => body.classList.toggle('purchase')
));

//open constructor
[document.getElementById('constructor'), document.getElementById('constructor-cross')].forEach( i =>
	i.addEventListener('click', e => body.classList.toggle('constructor')
));